import { useState, useEffect } from "react";
import { styled } from "@mui/system";
import { Typography, Popover, Grid } from "@mui/material";
import { ExpandMoreIcon } from "../../../spiritIcons";
import { getCookie } from "../../../spiritUtils";
import SpiritButton from "../../SpiritButton";

const StyledAddress = styled("div")`
  width: 100%;
  max-width: 400px;
  min-height: 50px;
  padding: 0;
  padding-left: 20px;
  padding-right: 10px;
  font-size: 20px;
  font-family: Raleway;
  border: solid 1px #202020;
  display: flex;
  &:focus {
    outline: none;
  }
  align-items: center;
  gap: 10px;
`;

const AddressText = styled("div")`
  max-width: 320px;
  flex-grow: 1;
`;

const PopoverItem = styled(Typography)`
  cursor: pointer;
  padding: 4px;
  &:hover {
    transform: translateY(-3%);
    transition: transform 0.5s ease;
  }
`;

const StyledPopover = styled(Popover)`
  & > .MuiPopover-paper {
    max-width: 400px;
    border-radius: 0px;
  }
`;

const InputWrapper = styled("div")`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-top: 35px;
  cursor: pointer;
`;

const AddressSelect = ({ error }) => {
  const [addresses, setAddresses] = useState(null);
  const [anchorEl, setAnchorEl] = useState(null);
  const [selected, setSelected] = useState(null);

  const open = Boolean(anchorEl);
  const csrftoken = getCookie("csrftoken");
  const token = localStorage.getItem("token");

  useEffect(() => {
    fetch(`${process.env.REACT_APP_BACKEND_DOMAIN}/address`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json;charset=utf-8",
        Authorization: `JWT ${token}`,
        "X-CSRFToken": csrftoken,
      },
    })
      .then((res) => res.json())
      .then((json) => {
        setAddresses(json.items);
      });
  }, [csrftoken, token]);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handlePopoverItemClick = (e) => {
    const selectedAddress = JSON.parse(e.currentTarget.getAttribute("value"));
    setSelected(selectedAddress);
    setAnchorEl(null);
  };

  const handleCheckoutClick = () => {
    fetch("https://spirit.ge:8000/buy_process", {
      method: "POST",
      headers: {
        "Content-Type": "application/json;charset=utf-8",
        Authorization: `JWT ${token}`,
        "X-CSRFToken": csrftoken,
      },
      body: JSON.stringify({
        shipping: { id: selected.id },
        language: localStorage.getItem("language")
          ? localStorage.getItem("language")
          : "en",
      }),
    })
      .then((res) => res.json())
      .then((json) => {
        window.location = json.link;
      });
  };

  return (
    <Grid item container>
      <InputWrapper>
        <Typography variant="body1" fontSize="20px" component="div">
          <b>Full address, Mobile number, Full name</b>
        </Typography>
        <StyledAddress
          style={{ border: error ? "solid 1px red" : "solid 1px #202020" }}
          spellCheck={false}
          // error
          onClick={(e) => setAnchorEl(e.currentTarget)}
        >
          <AddressText>
            {selected &&
              selected.address +
                ", " +
                selected.phone +
                ", " +
                selected.first_last_name}
            {!selected && "Click here to select your address"}
          </AddressText>
          <ExpandMoreIcon />
        </StyledAddress>
        <StyledPopover
          id="address-popover"
          open={open}
          anchorEl={anchorEl}
          onClose={handleClose}
          anchorOrigin={{
            vertical: "bottom",
            horizontal: "right",
          }}
          transformOrigin={{
            vertical: "top",
            horizontal: "right",
          }}
          elevation={2}
        >
          {addresses &&
            addresses.map((item) => (
              <PopoverItem
                key={item.id}
                value={JSON.stringify(item)}
                variant="body1"
                fontSize="20px"
                onClick={handlePopoverItemClick}
              >
                {item.address}, {item.phone}, {item.first_last_name}
              </PopoverItem>
            ))}
        </StyledPopover>
        {error && (
          <Typography
            variant="body1"
            fontSize="16px"
            color="red"
            component="div"
          >
            {error}
          </Typography>
        )}
      </InputWrapper>
      <Grid item container>
        <Grid item container xs={12} justifyContent="center">
          <SpiritButton
            sx={
              selected
                ? { mt: "35px" }
                : { mt: "35px", opacity: 0.5, pointerEvents: "none" }
            }
            onClick={handleCheckoutClick}
          >
            Proceed to checkout
          </SpiritButton>
        </Grid>
        <Grid item xs={12}>
          {!selected && (
            <Typography variant="body1" fontSize="20px" component="div" mt="15px" textAlign="center">
              <b>Note:</b> Address is not selected
            </Typography>
          )}
        </Grid>
      </Grid>
    </Grid>
  );
};

export default AddressSelect;
