import { Box, Grid, Backdrop } from "@mui/material";
import * as React from 'react';
import { styled } from "@mui/system";
import {SearchIcon, CloseIcon} from "../../spiritIcons"
import { useHistory } from "react-router-dom";


const SearchBarWrapper = styled('div')`
    background-color: #f9f9f9;
    display: flex;
    height: 45px;
    padding: 10px;
`

const Divider = styled('div')`
    border-left: 2px solid #202020;
    margin: 0 7px 0 9px;
`

const Input = styled('input')`
    flex-grow: 1;
    border: none;
    font-size: 22px;
    outline: none;
    margin-right: 5px;
    background-color: #f9f9f9;
    width: 400px;
`

const SearchBar = ({handleClose, mobile}) => {
    const history = useHistory();

    return (
        <SearchBarWrapper style={mobile ? {margin: '15px 0 15px 0'} : {}}>
            <Input placeholder="Search..." spellCheck={false} style={mobile ? {width: '100%'} : {}}/>
            <SearchIcon sx={{ cursor: 'pointer'}} onClick={() => history.push('/shop')} />
            <Divider />
            <CloseIcon onClick={() => handleClose(null)} sx={{ cursor: 'pointer'}} />
        </SearchBarWrapper>
    )
}

export default SearchBar;