import { MenuIcon } from '../../../spiritIcons';
import { Drawer, Typography, Grid, Popover} from '@mui/material/';
import * as React from 'react';
import { styled } from "@mui/system";
import { PhoneIcon, EmailIcon, LocationIcon, FacebookIcon, TelegramIcon, InstagramIcon } from '../../../spiritIcons'
import { useHistory } from "react-router-dom";
import SearchBar from '../SearchBar';

const HamburgerMobile = ({ white, handleModal, ...props }) => {
    const [state, setState] = React.useState(false)


    const isLoggedIn = sessionStorage.getItem('logged_in')
    const history = useHistory();

    const toggleDrawer = (toggle) => {
        console.log("hello");
        setState(toggle);
    };


    const CloseButton = styled('div')`
        cursor: pointer;
        font-size: 24px;
        margin-left: 368px;
    `

    const StyledLink = styled('div')`
        cursor: pointer;
        display: flex;
        justify-content: center;
        &:hover {
            &:before {
              display: inline-block;
              content: '';
              width: 23px;
              border-top: solid 1px #fbfbfb;
              margin-right: 15px;
              margin-top: 25px;
            }
            &:after {
              display: inline-block;
              content: '';
              width: 23px;
              border-top: solid 1px #fbfbfb;
              margin-left: 15px;
              margin-top: 25px;
            }
        }
    `

    const handleLinkClick = () => {
        history.push('/shop')
    }

    const handleProfileLinkClick = () => {
        history.push('/profile')
    }

    const StyledItems = styled('div')`
        border-bottom: 1px solid #202020;
        cursor: pointer;
        height: 30px;
    `

    const handleItemClick = (e) => {
        const item = e.currentTarget.getAttribute('value');
        switch(item){
            case 'home':
                history.push('/');
                break;
            case 'shop':
                history.push('/shop');
                break;
            case 'blog':
                history.push('/');
                break;
            case 'profile':
                history.push('/profile');
                break;
            case 'login':
                handleModal(true);
                setState(false);
                break;
            default:
                history.push('/');
                break;
        }
    } 



    return (
        <>
            <MenuIcon white={white ? true : false } onClick={() => toggleDrawer(true)} sx={{ cursor: 'pointer' }} {...props} />
            <Drawer
                anchor="top"
                open={state}
                onClose={() => toggleDrawer(false)}
                elevation={0}
                sx={{
                    '.MuiDrawer-paperAnchorTop': {
                        top: '63px',
                        paddingLeft: '15px',
                        paddingRight: '15px'
                    }
                }}
            >
                <StyledItems>
                    <Typography value="home" variant="body1" component="div" fontSize="16px" lineHeight={2} onClick={handleItemClick}><b>Home</b></Typography>
                </StyledItems>
                <StyledItems>
                    <Typography value="shop" variant="body1" component="div" fontSize="16px" lineHeight={2} onClick={handleItemClick}><b>Shop</b></Typography>
                </StyledItems>
                <StyledItems>
                    <Typography value="blog" variant="body1" component="div" fontSize="16px" lineHeight={2} onClick={handleItemClick}><b>Blog</b></Typography>
                </StyledItems>
                {
                    isLoggedIn
                    ?
                    <StyledItems>
                        <Typography value="profile" variant="body1" component="div" fontSize="16px" lineHeight={2} onClick={handleItemClick}><b>Profile</b></Typography>
                    </StyledItems>
                    :
                    <StyledItems>
                        <Typography value="login" variant="body1" component="div" fontSize="16px" lineHeight={2} onClick={handleItemClick}><b>Log In</b></Typography>
                    </StyledItems>
                }
                <Grid container item pt="15px">
                    <Typography variant="body2" fontSize="16px">EN</Typography>
                    <Typography variant="body2" fontSize="16px" sx={{
                        marginLeft: '7px',
                        color: '#9e9e9e'
                    }}>GEO</Typography>
                </Grid>
                <SearchBar mobile />
            </Drawer>
        </>
    )
}

export default HamburgerMobile;