import React from 'react';
import Breadcrumbs from '@mui/material/Breadcrumbs';
import Typography from '@mui/material/Typography';
import Link from '@mui/material/Link';
import {Link as BrowserRouter} from 'react-router-dom';

const SpiritBreadcrumb = (props) => {
    const {path} = props;

    console.log(path)

    return (
        <Breadcrumbs aria-label="breadcrumb">
            {
                path.map((item, i) => {
                    console.log(path.length > i)
                    if(path.length - 1 > i) {
                        return (
                            <Link key={item} component={BrowserRouter} underline="hover" color="#9a9a9a" to={`/${item.toLowerCase()}`} sx={{fontSize: '20px', cursor: 'pointer'}}>
                                {item}
                            </Link>
                        )
                    }
                    if(path.length - 1 <= i) {
                        return (
                            <Typography component={Link} underline="none" sx={{cursor: 'default'}} key={item} color="text.primary" fontSize="20px"><b>{item}</b></Typography>
                        )
                    }
                    return item;
                })
            }
        </Breadcrumbs>
    );
};

export default SpiritBreadcrumb;