import { useState, useEffect, useRef } from "react";
import { Typography, Grid } from "@mui/material";
import { styled } from "@mui/system";
import Footer from "../footer/Footer";
import Navbar from "../navbar/Navbar";
import useMediaQuery from "@mui/material/useMediaQuery";
import AddressAdd from "../Profile/Address/AddressAdd";
import CartCheckoutList from "../CartCheckoutList/CartCheckoutList";
import CartListItem from "../CartItemList/CartListItem";
import AddressSelect from "../Profile/Address/AddressSelect";
import { getCookie } from "../../spiritUtils";
import { useHistory } from "react-router-dom";
import { SeeOrderIcon, CloseIcon } from "../../spiritIcons";
import Modal from "@mui/material/Modal";
import SpiritBreadcrumb from '../SpiritBreadcrumb';

const ContentWrapper = styled("div")`
  max-width: 1770px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 75px;
`;

const Wrapper = styled("div")`
  background-color: #f5f5f5;
`;
const FooterWrapper = styled("div")`
  width: 100%;
  background-color: #202020;
  height: 100%;
  margin-top: 205px;
  color: #fbfbfb;
`;

const CheckoutPage = () => {
  const [cartItems, setCartItems] = useState(null);
  const history = useHistory();

  const [modalOpen, setModalOpen] = useState(false);
  const handleModalOpen = () => setModalOpen(true);
  const handleModalClose = () => setModalOpen(false);

  const isLoggedIn = sessionStorage.getItem("logged_in");
  const csrftoken = getCookie("csrftoken");

  if (!isLoggedIn) history.push("/");

  useEffect(() => {
    fetch(`${process.env.REACT_APP_BACKEND_DOMAIN}cart`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json;charset=utf-8",
        Authorization: `JWT ${localStorage.getItem("token")}`,
        "X-CSRFToken": csrftoken,
      },
    })
      .then((res) => {
        if (res.ok) {
          return res.json();
        } else {
          return false;
        }
      })
      .then((cartData) => {
        if (!cartData) return;
        fetch(
          `${process.env.REACT_APP_BACKEND_DOMAIN}/wineproduct/?wine=%27test%27`
        )
          .then((res) => {
            if (res.ok) {
              return res.json();
            } else {
              return false;
            }
          })
          .then((productData) => {
            if (!productData) return;
            let cart = [];
            productData.menu.forEach((productItem) => {
              cartData.items.forEach((cartItem) => {
                if (productItem.id === cartItem.product_id) {
                  cart.push({
                    id: productItem.id,
                    image: productItem.image,
                    name: productItem.name,
                    sales: productItem.sales,
                    quantity: cartItem.quantity,
                    inStockQuantity: productItem.quantity,
                    price: productItem.price,
                    type: productItem.type,
                    vendor: productItem.vendor,
                    color: productItem.color,
                  });
                }
              });
            });
            setCartItems(cart);
          });
      });
  }, [isLoggedIn, csrftoken]);

  const matches = useMediaQuery("(max-width:900px)");

  const handleCartUpdate = (id, payload) => {
    const updatedData = cartItems.map((item) => {
      if (item.id === id) {
        return {
          ...item,
          quantity: parseInt(payload),
        };
      }
      return item;
    });
    setCartItems(updatedData);
  };

  const handleCartDelete = (id) => {
    const updatedData = cartItems.filter((item) => item.id !== id);
    setCartItems(updatedData);
  };

  return (
    <Wrapper>
      <Navbar fixed />
      <ContentWrapper>
        <Grid item container>
          <SpiritBreadcrumb path={['Home', 'Cart']} />
        </Grid>
        {!matches && (
          <Grid item container spacing="52px">
            <Grid item xs={6} justifyContent="center">
              <Grid item container sx={{ maxWidth: "800px" }}>
                <Grid item>
                  <Typography variant="h2" fontSize="50px">
                    Contact
                  </Typography>
                </Grid>
                <Grid item container mt="30px">
                  <AddressAdd
                    handleComponentChange={(e) => console.log(e)}
                    checkout
                  />
                </Grid>
                <Grid item container justifyContent="center" mt="30px">
                  <Grid item container>
                    <Typography
                      variant="h2"
                      fontSize="50px"
                      sx={{ alignSelf: "flex-start" }}
                    >
                      Shipping
                    </Typography>
                  </Grid>
                  <AddressSelect />
                </Grid>
              </Grid>
            </Grid>
            <Grid item xs={6}>
              <Typography variant="h2" fontSize="50px">
                My Order
              </Typography>
              <Grid
                item
                container
                sx={{
                  maxHeight: "580px",
                  overflowY: "auto",
                  gap: "15px",
                  mt: "30px",
                }}
              >
                {cartItems &&
                  cartItems.length > 0 &&
                  cartItems.map((item) => (
                    <CartListItem
                      checkout
                      key={item.id}
                      data={item}
                      handleUpdate={(id, count) => handleCartUpdate(id, count)}
                      handleDelete={(id) => handleCartDelete(id)}
                    />
                  ))}
              </Grid>
              <CartCheckoutList data={cartItems} />
            </Grid>
          </Grid>
        )}
        {matches && (
          <Grid item container>
            <Grid item xs={12}>
              <Grid item container xs={12}>
                <Grid item xs={6}>
                  <Typography variant="h2" fontSize="35px">
                    Contact
                  </Typography>
                </Grid>
                <Grid
                  item
                  container
                  xs={6}
                  justifyContent="flex-end"
                  gap="5px"
                  sx={{ cursor: "pointer" }}
                  onClick={handleModalOpen}
                >
                  <Typography variant="h2" fontSize="16px">
                    See order
                  </Typography>
                  <SeeOrderIcon />
                </Grid>
                <Modal
                  open={modalOpen}
                  onClose={handleModalClose}
                  sx={{overflow: 'scroll'}}
                >
                  <Grid item container sx={{ backgroundColor: "white", padding: "20px", mt: '120px' }} justifyContent="flex-end">
                    <Grid item sx={{cursor: 'pointer'}} onClick={handleModalClose}>
                      <CloseIcon  />
                    </Grid>
                    <Grid
                      item
                      container
                      sx={{
                        maxHeight: "580px",
                        overflowY: "auto",
                        gap: "15px",
                        mt: "30px",
                        mb: "15px"
                      }}
                    >
                      {cartItems &&
                        cartItems.length > 0 &&
                        cartItems.map((item) => (
                          <CartListItem
                            checkout
                            key={item.id}
                            data={item}
                            handleUpdate={(id, count) =>
                              handleCartUpdate(id, count)
                            }
                            handleDelete={(id) => handleCartDelete(id)}
                          />
                        ))}
                    </Grid>
                    <CartCheckoutList data={cartItems} />
                  </Grid>
                </Modal>
              </Grid>
            </Grid>
            <Grid item xs={12}>
              <AddressAdd handleComponentChange={(e) => console.log(e)} />
            </Grid>
            <Grid item xs={12} sx={{ mt: "64px" }}>
              <Grid item xs={12}>
                <Typography variant="h2" fontSize="35px">
                  Shipping
                </Typography>
              </Grid>
              <Grid item xs={12}>
                <AddressSelect />
              </Grid>
            </Grid>
          </Grid>
        )}
      </ContentWrapper>
      <FooterWrapper>
        <div style={{ padding: "0 15px 0 15px" }}>
          <Footer />
        </div>
      </FooterWrapper>
    </Wrapper>
  );
};

export default CheckoutPage;
