import React, {useState, useEffect} from 'react';
import AddressInput from './AddressInput';
import { Typography, Grid } from "@mui/material";
import { styled } from "@mui/system";
import SpiritButton from '../../SpiritButton';
import SpiritInput from '../../SpiritInput';
import SpiritTextArea from '../../SpiritTextArea';
import {getCookie} from '../../../spiritUtils';


const AddressAdd = props => {
    const {handleComponentChange, checkout} = props;
    const [name, setName] = useState('');
    const [mobileNumber, setMobileNumber] = useState('');
    const [fullAddress, setFullAddress] = useState('');
    const [comment, setComment] = useState('');
    const [formErrors, setFormErrors] = useState({
        name: '',
        mobileNumber: '',
        fullAddress: '',
        comment: '',
    })


    const resetForm = () => {
      setName("");
      setMobileNumber("");
      setComment("");
      setFullAddress("");
    };

    const changeFormState = (setState, newState, field) => {
      setState(prevState => {
          const stateObj = Object.assign({}, prevState)
          stateObj[field] = newState
          return stateObj;
      })
    }
    
    const validateInputs = () => {
        let valid = true;
      
        if(name === '') {
            changeFormState(setFormErrors, 'This field shouldn\'t be blank', 'name');
            valid = false;
        }
        if(mobileNumber === '') {
            changeFormState(setFormErrors, 'This field shouldn\'t be blank', 'mobileNumber');
            valid = false;
        }
        if(fullAddress === ''){
            changeFormState(setFormErrors, 'This field shouldn\'t be blank', 'fullAddress');
            valid = false;
        }

        return valid
    }

    const handleSubmit = () => {
        setFormErrors({
            name: '',
            mobileNumber: '',
            fullAddress: '',
            comment: '',
        })
        const validation = validateInputs();
        if(validation){
            const data = { action: 'add', info: { address: fullAddress, phone: mobileNumber, first_last_name: name, comment: comment } }
            fetch(`${process.env.REACT_APP_BACKEND_DOMAIN}address/`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json;charset=utf-8',
                    Authorization: `JWT ${localStorage.getItem('token')}`,
                    'X-CSRFToken':  getCookie('csrftoken'),
                },
                body: JSON.stringify(data)
            })
            .then(res => {
                if(!res.ok) console.log('something went wrong', res)
                return res.json()
            })
            .then(json => {
                handleComponentChange('view');
                resetForm();
            })
        }
    }
    
    


    return (
      <Grid component={"form"} item container onSubmit={handleSubmit}>
        <Grid item container justifyContent="center" gap="22px" mt="15px">
            {
              checkout ? 
              <Grid item container spacing="30px">
                <Grid item xs={6}>
                  <SpiritInput
                    label="Full name*"
                    value={name}
                    error={formErrors.name}
                    onChange={(e) => setName(e.target.value)}
                  />
                </Grid>
                <Grid item xs={6}>
                  <SpiritInput
                    label="Phone number*"
                    value={mobileNumber}
                    error={formErrors.mobileNumber}
                    onChange={(e) => setMobileNumber(e.target.value)}
                  />
                </Grid>
              </Grid>
              :
              <>
                <SpiritInput
                  label="Full name*"
                  value={name}
                  error={formErrors.name}
                  onChange={(e) => setName(e.target.value)}
                />
                <SpiritInput
                  label="Phone number*"
                  value={mobileNumber}
                  error={formErrors.mobileNumber}
                  onChange={(e) => setMobileNumber(e.target.value)}
                />
              </>
            }
          
          <SpiritInput
            label="Full address*"
            error={formErrors.fullAddress}
            value={fullAddress}
            onChange={(e) => setFullAddress(e.target.value)}
          />
          <SpiritTextArea
            label="Comment"
            value={comment}
            error={formErrors.comment}
            onChange={(e) => setComment(e.target.value)}
            placeholder="Anything that you think could help us with delivery..."
          />
        </Grid>
        <Grid item container justifyContent="center" pt="48px">
          <SpiritButton onClick={handleSubmit}>Save</SpiritButton>
        </Grid>
      </Grid>
    );
};



export default AddressAdd;